body{
    background-color:royalblue;
}
.signup{
    background-color:rgb(20, 20, 20); 
    /* for glassy transperent effect add "a" and the num*/
    /* background: linear-gradient(to left,white, rgb(231, 38, 64)); */
    color:rgb(255, 255, 255);
    height:210px;
    width:400px;
    border: 5px solid rgb(248, 245, 245);
    border-radius: 10px;
    padding: 10px;
    text-align:center;
    margin: auto;
    margin-top: 200px;
    padding-bottom: 20px;
    /* opacity: 0.2; */
    animation: shadow-pop-tr 0.3s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;        }
.signup div{
    margin-bottom: 10px;
}
