.body {
    display:flex;
    flex-wrap: wrap;
}

.emp-card-template {
    /* flex:1; */
    display: flex;
    flex-direction: column;
    justify-items: center;
    background-color: black;
    border: rgb(113, 115, 115) solid 3px;
    border-radius: 25px;
    height: 350px;
    width: 270px;
    color: white;
    text-align: center;
    margin: 15px 15px;
}

.emp-card-template img {
    /* justify-items: center; */
    margin:auto;
    border-radius: 5px;
    height: 200px;
    width: 200px;
    /* padding: 30px;
    clip-path: circle(35%); */
    text-align: center;
}

.emp-name {
    padding: 5px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 20px;
}

