

.calculator {
    background-color: rgb(0, 0, 0);
    height: 300px;
    width: 250px;
    text-align: center;
    padding: 30px;
    padding-bottom: 50px;
    margin: auto;
    /* opacity: 0.7; */
    border-radius: 20px;
    margin-top: 100px;
    margin: bottom 100px;
    border: 5px solid rgb(255, 252, 252);
    /* background-image: url(calcu.jpg); */
    /* animation: scale-up-top 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; */
}

.calculator div input {
    font-size: 30px;
    margin: 2px;
}

#display-area {
    width: 220px;
    height: auto;
    margin: 5px;
    margin-bottom: 20px;
}

.calculator .button1 {
    height: 50px;
    width: 50px;
}

.calculator .button2 {
    height: 50px;
    width: 107px;
    margin-bottom: 20px;
}

.calculator input:active {
    animation: scale-up-top 2s 1;
}

.calculator #setValue-area {
    width: 90%;;
}