div {
    color:white;
}

tr,th,td{
    border:1px white;
    margin:5px;
    
}

.page-nav {
    display:flex;
    justify-content: center;
}

.page-nav button {
    margin:3px;
    padding:10px;
    border-radius: 5px;
}

.page-nav select {
    margin:3px;
    padding:10px;
    border-radius: 5px;
}

.activePage{
    background-color: rgb(44, 44, 46);
    color: aliceblue;
}

button disabled{
    opacity:0.5;
    cursor:not-allowed;
}

table{
    border: 1px solid white;
    margin:50px;
    padding:5px;
    text-align:center;
  }